.Modal {
    position: fixed;
    z-index: 500;
    background-color: #ddd;
    width: 80%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 10%;
    top: 15%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    border-radius: 10px;
}

.show{
    transform: translateY(0);
    opacity: 1;
}

.hide{
    transform: translateY(-100vh);
    opacity: 0;
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}